.question-container{
    display:flex;
    align-items: center;
  }

  
.questions-drop-down{
  display:flex;
  align-items: center;
}

.question{
  font-size:1.3rem;
  margin-bottom: 1rem;
  font-weight:600;
  color:#2c385e;
}
.parent-dropdown{
  display:flex;
  align-items: center;
}
.question-dropdown{
  color:rgb(121, 121, 121);
}
.drop{
	display: block;
	float: left;
	padding: 1rem 0 1rem 1rem;
  position: relative;
  z-index:1000;
	text-decoration: none;
  transition-duration: 0.5s;
}
 
.parent-dropdown:hover,
.parent-dropdown:focus-within {

	cursor: pointer;
}

.drop:focus-within a {
  outline: none;
}

.dropdown {
	visibility: hidden;
  opacity: 0;
  min-width: 10rem;
	position: absolute;
  transition: all 0.5s ease;
  margin-top: 1rem;
	left: 0;
  display: none;
  border: 1px solid #efefef;
  border-bottom: 1px solid #666666;
  border-left: 1px solid #666666;
  overflow-y: scroll;
  max-height:40vh;
}
::-webkit-scrollbar {
  width: 2px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(190, 190, 190); 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
.dropdown-container:hover > ul,
.dropdown-container:focus-within > ul,
.dropdown:hover,
.dropdown:focus {
   visibility: visible;
   opacity: 1;
   display: block;
}

.dropdown-item {
	clear: both;
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #efefef;
  transition: all .2s;
}
.dropdown-item:hover{
  background: #efefef;
}
.underline{
  height:1px;
  background:rgb(187, 187, 187);
  width:100%;
  margin-top:0.5rem;
}
.dropdown-icon{
  margin-left:2rem;
  color:rgb(104, 104, 104);
}
