.next-button{
    background:#4b9fff;
    color:#fff;
    padding: 1rem;
    cursor: pointer;
    transition: all .2s;
    border: none;
  }
  .next-button:hover{
    background:#4b9fff;
  }
  .next-button:hover {
    transform: translateY(-1px);
    box-shadow: 0 .5rem 1rem rgba(155, 155, 155, 0.2); }
    .next-button:hover::after {
      transform: scaleX(1.4) scaleY(1.6);
      opacity: 0; }
  
  .next-button:active, .next-button:focus {
    outline: none;
    transform: translateY(-0.5px);
    box-shadow: 0 0.2rem .4rem rgba(156, 156, 156, 0.2); }

.end-continue-button-container{
  display:flex;
  width:100%;
}
.end-continue-button{
  width:100%;
  text-align: center;
  padding: 0.7rem 1rem;
  border-top:1px solid #efefef;
  border-left:.5px solid #efefef;
  border-right:.5px solid #efefef;
  color: #096ddf;
  font-weight:500;
  font-size:.8rem;
}  
.end-continue-button:hover{
  background: #efefef;
  cursor: pointer;
}
.add-test-button{
  width: 4rem;
  height: 1.5rem;
  background:#4b9fff;
  color:#fff;
  padding: 1rem;
  border:none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size:.8rem;
  cursor: pointer;
}
.add-test-button:hover{
  background:#4b9fff;
}
.add-test-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 .5rem 1rem rgba(155, 155, 155, 0.2); }
  .add-test-button:hover::after {
    transform: scaleX(1.4) scaleY(1.6);
    opacity: 0; }

.add-test-button:active, .add-test-button:focus {
  outline: none;
  transform: translateY(-0.5px);
  box-shadow: 0 0.2rem .4rem rgba(156, 156, 156, 0.2); }
.page-button-container-container{
  width:90%;
  display:flex;
  justify-content: center;
}
.page-button-container{
  width:90%;
}
.page-button{
  padding: .4rem .8rem;
  font-size: .7rem;
  border-radius: 10rem;
  background:#fff;
  border:none;
  display:flex;
  align-items: center;
  
  margin-top:1rem;
  justify-content: center;
  cursor: pointer;
}
.page-button:focus {outline:0;}
.page-button:hover{
  background:#2c385e;
  color:#fff;
  transition: all .2s;
}

.results__btn--next{
 float:right;
 padding-left:1.4rem;
}
.results__btn--prev{
  float:left;
  flex-direction: row-reverse;
  padding-right:1.4rem;
}