.display-result{
    display:flex;
    flex-direction: column;
    align-items: center;
  }
  .display-quizzes{
    margin: 1rem 5rem;
    padding :1rem 3rem;
    border:1px solid #E4E4E4;
    box-shadow: 0px 0px 4px #BEBEBE;
    width:90%;
    height:75vh;
    position: relative;
  }

  .display-result__title{
    font-weight: 600;
    font-size:1.5rem;
    color:#2c385e;
    margin-bottom:1.5rem;
  }
  .display-result__yourScore{
    font-weight: 600;
    font-size:1.3rem;
    color:#2c385e;
    margin:1rem 0;
    
  }

  .display-dialog-container{
    position: absolute;
    width:100%;
    height:100%;
    top:0;
    left:0;
    background:rgba(0, 0, 0, 0.589);
    z-index:1500;
    display:flex;
    justify-content: center;
    align-items: center;
  }
  .display-dialog{
    background:white;
    width:20rem;
    height:8rem;
    border-radius:3px;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  
  .display-dialog__title{
    margin-top:2rem;
    font-size:1rem;
  }
  