.form-container{
    background: #fff;
    width:90%;
    height:10rem;
    margin-bottom: 1rem;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .form{
    display:flex;
    flex-direction: column;
    align-items: center;
    width: 90%;
  }
  .form__create-text{
    color: #7a7a7a;
    margin-bottom:0.5rem;
  }
  .input{
    width: 100%;
    margin-bottom: 0.5rem;
    border-radius: 3px;
    border: 1px solid #efefef;
    padding-left:.5rem;
    padding-right:.5rem;
    caret-color: #a5a5a5;
    color: #4b4b4b;
    height:2rem;
    transition: all .2s;
  }
  .input:hover{
    border: 1px solid #a1a1a1;
  }
  .input::placeholder{
    color: #a5a5a5;
    font-size:.8rem;
  }