.display-test-list{
    display:flex;
    padding: 1rem 1rem ;
    background: #fff;
    color:#2c385e;
    width:90%;
    align-items: center;
    font-size: 12px;
    border-bottom: 1px solid #efefef;
    cursor: pointer;
    transition: all .2s;
  }
  
  .remove-icon{
    display:none
  }
  
  .display-test-list:hover,.test-selected{
    background: #2c385e;
    color:#fff;
  }
  
  .display-test-list:hover>.test-name-date>.test-name,
  .test-selected>.test-name-date>.test-name{
    color:#fff;
  }
  .display-test-list:hover>.remove-icon,
  .test-selected>.remove-icon{
    color:#fff;
    display:inline;
  }
  .display-test-list:hover>.remove-icon:hover,
  .test-selected>.remove-icon:hover{
    color:rgb(163, 163, 163);
  }
  
  .test-name{
    font-weight: 600;
    font-size: 1.1rem;
    color:#2c385e
  }
  .test-date{
    font-size: 0.8rem;
    font-weight: 600;
    color:#8f8f8f;
  }
    