.answers{
  list-style: none;
  color:#686868;
  width:42vw;
}
.answer{
    display:flex;
    align-items: center;
    border:1px solid #c8c8c8;
    margin-bottom: 0.5rem;
    height: 4.5vh;
    position: relative;
    transition: all .2s;
  }
  .answer-hoverable:hover{
    border:1px solid rgb(59, 59, 59);
    color:#595959;
    cursor: pointer;
  }
  .answer-hoverable:hover>.answer__character{
    border-right:1px solid rgb(59, 59, 59);
    color:#595959;
    cursor: pointer;
  }
  .answer__character{
    height:100%;
    width: 5%;
    border-right:1px solid #c8c8c8;
    display:flex;
    align-items: center;
    justify-content:center;
  }
  
  .display-none{
    display:none
  }
  .answer__choice{
    margin-left: 2rem;
    font-size: 15px;
  }
  .answer__result{
    display:none;
  }
  .answer__result-word{
    font-size:12px;
    position: absolute;
    right: 1.6rem;
    bottom: 5px;
  }
  
  .correct-answer{
    border:1px solid green
  }
  .wrong-answer{
    border:1px solid red
  }
 