*,
*::after,
*::before {
  margin :0;
  padding:0;
  box-sizing: border-box;
}

.container {
  height:100vh;
  background: #efefef;
}
.nav-bar {
  background: #4338C2;
  height: 12.5%;
  margin-bottom:2rem;
}

.test-img{
  height:3rem;
}

.check-img{
  height:2rem;
  margin-right: 1rem;
}

.check-error-img{
  height:1.6rem;
  margin-left:1rem;
}
.check-error-img__character{
  height:1.1rem;
}
.ending-img{
  width:40%;
}
.start-img{
  width:20%;
}
.questionImg{
  
    width:8rem;
    height:8rem;
    grid-column-start:2;
    justify-self: center;
    
    margin-left:5rem;
    transition: transform .2s;
    background:#969696;
}
.question-img{
  width:100%;
  transition: transform .2s;
}
.question-img:active{
  position: relative;
  transform: translateX(-20rem) translateY(-5rem) scale(3.2);
  z-index: 1000;
}

.quiz-container{
  height:100%
}